*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  width: 100vw;
  height: 100vh;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-size: 40px;
  background-color: bisque;
  display: flex;
  align-items: center;
  justify-content: center;
}
.app_container{
  background-color: rgba(205, 248, 255, 0.301);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #fff;
  width: 350px;
  height: 400px;
  border-radius: 2.5em;
  border: 5px solid #fff;
  box-shadow: -10px 5px 5px  rgba(0, 0, 0,.8),
  10px 5px 5px  rgba(0, 0, 0,.8);
}
.temp_container{
  height: 170px;
  width: 170px;
  border-radius: 50%;
  border: 5px solid #fff;
  display: flex;
  align-items: center;
  justify-content: center;
 
}
.button_container{
  display: flex;
}
button{
outline: none;
border: 2px solid #fff;
  margin: 5px;
  padding: 10px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: orange;
  font-weight: bold;
  font-size: 40px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
}
button:hover{
opacity: .5;
}
@media screen and (max-width: 400px) {
  .app_container{
    width: 280px;
    height: 320px;
    border-radius: 2em;
  }
}
@media screen and (max-width: 320px) {
  .app_container{
    width: 250px;
    height: 300px;
    border-radius: 1.8em;
  }
}
@media screen and (max-width: 280px) {
  .app_container{
    width: 200px;
    height: 280px;
    border-radius: 1.5em;
  }
}